import { Box, Grid } from '@mui/material';
import React from 'react';
import { Outlet } from 'react-router-dom';
import Footer from '../footer';
import Header from '../header';

const OutletCastome = () => {
  return (
    <Box
      sx={{
        display: 'flex',
        minHeight: '100vh',
        flexDirection: 'column',
      }}
    >
      <Header />

      <Box
        sx={{
          flex: 'auto',
        }}
      >
        <Outlet />
      </Box>

      <Footer />
    </Box>
  );
};

export default OutletCastome;
