import {
  AppBar,
  Box,
  Button,
  Container,
  IconButton,
  Menu,
  MenuItem,
  Toolbar,
  Tooltip,
  Typography,
} from '@mui/material';
import React from 'react';
import MenuIcon from '@mui/icons-material/Menu';
import { contentHeader } from '../../../helpers/content';
import ContainerCustom from '../container';
import { NavLink } from 'react-router-dom';

const Header = () => {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  return (
    <>
      <div
        style={{
          backgroundColor: 'none',
        }}
      >
        <ContainerCustom>
          <AppBar
            position="static"
            sx={{
              height: `70px`,
              justifyContent: 'center',
              backgroundColor: 'inherit',
              boxShadow: 0,
            }}
            className="header"
          >
            <Toolbar className="header__content">
              <div className="header__block">
                <h2 className="header__title">Курсовой проект</h2>
                <div className="header__icon">
                  <svg
                    viewBox="0 0 30 30"
                    id="_24_-_Save"
                    data-name="24 - Save"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      id="Path_272"
                      data-name="Path 272"
                      d="M31,10.652a3,3,0,0,0-.962-2.2L22.854,1.8A3,3,0,0,0,20.816,1H4A3,3,0,0,0,1,4V28a3,3,0,0,0,3,3H28a3,3,0,0,0,3-3Zm-2,0V28a1,1,0,0,1-1,1H4a1,1,0,0,1-1-1V4A1,1,0,0,1,4,3H20.816a1,1,0,0,1,.68.266l7.183,6.652A1,1,0,0,1,29,10.652Z"
                      transform="translate(-1 -1)"
                      fillRule="evenodd"
                    />
                    <path
                      id="Path_273"
                      data-name="Path 273"
                      d="M17,2a1,1,0,0,0-1-1H7.6a1,1,0,0,0-1,1V4.667a3,3,0,0,0,3,3H14a3,3,0,0,0,3-3V2ZM15,3V4.667a1,1,0,0,1-1,1H9.6a1,1,0,0,1-1-1V3H15Z"
                      transform="translate(-1 -1)"
                      fillRule="evenodd"
                    />
                    <path
                      id="Path_274"
                      data-name="Path 274"
                      d="M23.215,22.667a3,3,0,0,0-3-3h-8.43a3,3,0,0,0-3,3V30a1,1,0,0,0,1,1h12.43a1,1,0,0,0,1-1Zm-2,0V29H10.785V22.667a1,1,0,0,1,1-1h8.43a1,1,0,0,1,1,1Z"
                      transform="translate(-1 -1)"
                      fillRule="evenodd"
                    />
                  </svg>
                </div>
              </div>
              <Box
                className="header__menu-1"
                boxShadow={0}
              >
                {contentHeader.map((item, index) => (
                  <NavLink
                    to={`${item.src}`}
                    key={index}
                  >
                    {' '}
                    <Button
                      className="header__button"
                      key={index}
                      sx={{
                        color: '#fff',
                        fontWeight: '400',
                        textTransform: 'none',
                        marginRight: {
                          desktop: `54px`,
                          laptop: `5px`,
                          table: `5px`,
                          mobile: `5px`,
                        },
                      }}
                    >
                      {item.name}{' '}
                    </Button>
                  </NavLink>
                ))}
              </Box>
              <Box
                className="header__menu-2"
                boxShadow={0}
              >
                <Tooltip title="Меню контета">
                  <Button aria-haspopup="true">
                    <MenuIcon
                      sx={{
                        width: `32px`,
                        height: `32px`,
                        color: 'white',
                      }}
                      onClick={handleClick}
                      aria-controls={open ? 'account-menu' : undefined}
                      aria-haspopup="true"
                      aria-expanded={open ? 'true' : undefined}
                    />
                  </Button>
                </Tooltip>

                <Menu
                  open={open}
                  onClose={handleClose}
                  onClick={handleClose}
                  transformOrigin={{ horizontal: 'right', vertical: 'top' }}
                  anchorOrigin={{ horizontal: 'right', vertical: 'top' }}
                  PaperProps={{
                    elevation: 0,
                    sx: {
                      overflow: 'visible',
                      filter: 'drop-shadow(0px 2px 8px rgba(0,0,0,0.32))',
                      mt: 5,
                      '& .MuiAvatar-root': {
                        width: 32,
                        height: 32,
                        ml: -0.5,
                        mr: 1,
                      },
                      '&:before': {
                        content: '""',
                        display: 'block',
                        position: 'absolute',
                        top: 0,
                        right: 35,
                        width: 10,
                        height: 10,
                        bgcolor: 'background.paper',
                        transform: 'translateY(-50%) rotate(45deg)',
                        zIndex: 0,
                      },
                    },
                  }}
                >
                  {contentHeader.map((item, index) => (
                    <MenuItem key={index + 1}>
                      <NavLink to={'/'}>{item.name}</NavLink>
                    </MenuItem>
                  ))}
                </Menu>
              </Box>
            </Toolbar>
          </AppBar>
        </ContainerCustom>
      </div>
    </>
  );
};

export default Header;
