import { Grid, IconButton, ListItemButton, Paper, styled } from '@mui/material';
import { Box } from '@mui/system';
import React from 'react';
import TelegramIcon from '@mui/icons-material/Telegram';
import GitHubIcon from '@mui/icons-material/GitHub';
import ContainerCustom from '../container';

const Footer = () => {
  return (
    <ContainerCustom>
      <Box
        display="grid"
        className="footer"
        gridTemplateColumns="repeat(12, 1fr)"
        alignItems={'center'}
      >
        <Box
          gridColumn="span 5"
          className="footer__block-1"
        >
          <h2 className="footer__title">Курсовая работа</h2>
        </Box>
        <Box
          className="footer__block-2"
          gridColumn="span 7"
          sx={{
            display: 'flex',
            justifyContent: 'flex-end',
          }}
        >
          <Box>
            <IconButton
              sx={{
                width: `3.125em`,
                height: `3.125em`,
                backgroundColor: 'custom.light',
                marginRight: `15px`,
                '&:hover .MuiSvgIcon-root': {
                  color: 'custom.light',
                },
              }}
            >
              <a href="https://t.me/dshoul13">
                <TelegramIcon
                  sx={{
                    fontSize: '1.875rem',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    color: 'custom.contrastText',
                    '&:hover': {
                      color: 'custom.light',
                    },
                  }}
                />
              </a>
            </IconButton>
            <IconButton
              sx={{
                width: `3.125em`,
                height: `3.125em`,
                backgroundColor: 'custom.light',
                '&:hover .MuiSvgIcon-root': {
                  color: 'custom.light',
                },
              }}
            >
              <a href="https://github.com/dhsoul13">
                <GitHubIcon
                  sx={{
                    color: 'custom.contrastText',
                    fontSize: '1.875rem',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                  }}
                />
              </a>
            </IconButton>
          </Box>
        </Box>
        <Box
          className="footer__block-3"
          gridColumn="span 12"
          sx={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <div className="footer__line"></div>
          <h2 className="footer__title-info">Выполнил: Мухреев Даниил</h2>
        </Box>
      </Box>
    </ContainerCustom>
  );
};

export default Footer;
